export default {
  data () {
    return {
      user: {}
    }
  },
  methods: {
    isloaded () {
      this.$http.get('/api/user/isLogin').then((response) => {
        if (response.data.status === 10000) {
          this.user = response.data.data
        }
      })
    },
    loginOut () {
      this.$http.post('/api/user/logout')
      this.$router.push('/login')
    },
    toMyActivity (id) {
      this.$router.push({
        path: '/personal/history?id=' + id
      })
    },
    toMyMessage (id) {
      this.$router.push({
        path: '/personal/message?id=' + id
      })
    }
  },
  created () {
    this.isloaded()
  }
}



