
import qs from 'qs'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {
components: {
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
SwiperItem,
XDialog,
Toast,
Group,
XSwitch
},
data () {
return {
products: [


















],
count: 1,
hasNext: true,
error: false,
position: 'default',
message: '',
id: 0,
isChange: false,
filtrate: false,
choiceTagName: '全部',
tagsList: [],
choiceTag: 0
}
},
methods: {
toDetail (id) {
this.$router.push({
path: '/activity?id=' + id
})
},
getlist () {
if (!this.hasNext) {
this.showError('没有更多信息')
this.hasNext = false
} else {
this.$http.post('/api/activity/list?pers=10&page=' + this.count).then((response) => {
this.$vux.loading.hide()
this.products = this.products.concat(response.data.data)
this.hasNext = response.data.hasNext
if (this.hasNext) {
this.count++
}
})
}
},
refreshlist () {
let i
if (this.hasNext) {
i = this.count - 1
} else {
i = this.count
}
this.$http.post('/api/activity/list?pers=' + i * 10, qs.stringify({
TagId: this.choiceTag
})).then((response) => {
this.$vux.loading.hide()
this.products = response.data.data
this.hasNext = response.data.hasNext
})
},
showError (msg) {
this.message = msg
this.error = true
}
},
created () {
this.$vux.loading.show({
text: '加载中'
})
},
mounted () {
this.getlist()
},
filters: {
describeOver: function (value) {
if (value[0].length > value[1]) {
return value[0].substring(0, value[1]) + '...'
} else {
return value[0]
}
}
},
watch: {
'$route' (curVal) {
if (curVal.path === '/travel') {
this.refreshlist()
}
}
}
}



