
import qs from 'qs'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
Toast
},
data () {
return {
recommend: '',
status: 0,
detail: {












},
Mobile: '',
id: 0,
wrapper: [],
content: '',
isLoad: true,
disbuy: false,
condition: '活动报名中',
day: 0,
hour: 0,
minute: 0,
second: 0,
intDiff: 10,
end: 0,
isCollect: false,
nowtime: 0,
isThumb: false,
comments: [],
acceptimages: [],
error: false,
position: 'default',
message: '',
isLoaded: false,
imglist: [],
notNextTick: true,
swiperOption: {

autoplay: 3000,

grabCursor: true,
setWrapperSize: true,
autoHeight: true,
pagination: '.swiper-pagination',
paginationClickable: true,

mousewheelControl: true,
observeParents: true,


debugger: true,


onTransitionStart (swiper) {

}
}
}
},
methods: {
isLogin () {
this.$http.get('/api/user/isLogin').then((response) => {
if (response.data.status === 10000) {
this.isLoaded = true
}
})
},
getdetail () {
this.$http.get('/api/activity/detail?id=' + this.id).then((response) => {
this.detail = response.data.data
this.judgeCondition()
this.judgeTimelimit()
this.imglist = this.detail.ImgUrl.split(',')
})
},

changTime (time) {
time = time.substring(0, 10)
let stringTime = time
let timestamp = Date.parse(new Date(stringTime))
return timestamp
},

gonext () {
if (!this.isLoaded) {
this.showError('请先登陆')
return
}
this.$http.get('/api/user/signup?id=' + this.id).then((response) => {
if (response.data.status === 10000) {
this.showError('报名成功')
} else if (response.data.status === 10001) {
this.showError('抱歉，该账号已被限制报名')
} else if (response.data.status === 10007) {
this.showError('不得重复报名同一活动')
} else if (response.data.status === 10003) {
this.showError('活动报名已结束')
} else if (response.data.status === 10004) {
this.showError('该活动已失效')
} else if (response.data.status === 10005) {
this.showError('抱歉，该活动人数已满')
} else {
this.showError('报名失败，请重试')
}
})
},

judgeCondition () {
if (this.detail.UserLimit - this.detail.UserCount <= 0) {
this.condition = '抱歉，活动人数已满'
this.disbuy = true
}
},
judgeTimelimit () {
var star = Number(this.changTime(this.detail.StartTime))
this.nowtime = Date.parse(new Date())
if (this.nowtime < star) {
this.condition = '活动报名中'
} else if (this.nowtime > this.end) {
this.condition = '活动报名时间已结束'
this.disbuy = true
}
},
showError (msg) {
this.message = msg
this.error = true
},
},
created () {
this.id = this.$route.query.id
this.getdetail()
this.isLogin()
},
filters: {
onlyData: function (value) {
return value.substring(0, 10)
},
describeOver: function (value) {
if (value[0].length > value[1]) {
return value[0].substring(0, value[1]) + '...'
} else {
return value[0]
}
}
},
computed: {
swiper () {
return this.$refs.mySwiper.swiper
}
}
}



