  export default {
    data () {
      return {
        message: [
//          {
//            Status: 0,
//            Activity: {
//              title: "123123"
//            },
//            CreatedTime: "2018-05-18T08:44:44+08:00"
//          },
//          {
//            Status: 1,
//            Activity: {
//              title: "asdasd"
//            },
//            CreatedTime: "2018-05-18T08:44:44+08:00"
//          }
        ]
      }
    },
    methods: {
      getlist () {
        this.$http.get('/api/user/message/list').then((response) => {
          this.list = response.data.data
          for (let i = 0; i < this.list.length; i++) {
            if (this.list[i].Content.substring(0, 6) === '管理员回复：') {
              this.list[i].Content = '您收到一条来自管理员的消息，请查看。'
            } else {
              let index = this.list[i].Content.indexOf('在<')
              this.list[i].Content = '用户：' + this.list[i].Content.substring(0, index) + '回复了您，快去看看吧！'
            }
          }
        })
      }
    },
    created () {
      this.getlist()
    },
    filters: {
      onlyData: function (value) {
        return value.substring(0, 10)
      }
    }
  }



