import qs from 'qs'
export default {
  data () {
    return {
      topath: '',
      isHome: 1,
      mobile: '',
      postpath: ''
    }
  },
  name: 'app',
  created () {
    this.nowPosition(this.$route.path)
  },
  watch: {
    '$route' (to) {
      this.nowPosition(to.path)
    }
  },
  methods: {
    nowPosition (path) {
      if (path.indexOf('home') !== -1) {
        this.isHome = 1
      } else if (path.indexOf('travel') !== -1 || path.indexOf('activity') !== -1) {
        this.isHome = 2
      } else if (path.indexOf('login') !== -1 || path.indexOf('register') !== -1 || path.indexOf('personal') !== -1) {
        this.isHome = 3
      }
    }
  }
}



